.digit-cell-normal,
.digit-cell-selected,
.digit-cell-highlighted {
	cursor: pointer;
	transition: 250ms;
	user-select: none;
}

.digit-cell-normal:not(.digit-cell-answer),
.digit-cell-selected:not(.digit-cell-answer),
.digit-cell-highlighted:not(.digit-cell-answer) {
	font-weight: bold;
}

.digit-cell-normal {
	background-color: #fafafa;
}

.digit-cell-selected {
	background-color: #90caf9;
}

.digit-cell-answer {
	color: #1565c0;
}

.digit-cell-highlighted {
	background-color: #e3f2fd;
}

@media (hover: hover) {
	.digit-cell-normal:hover,
	.digit-cell-selected:hover,
	.digit-cell-highlighted:hover {
		background-color: #b2ebf2;
	}
}
